@import url('https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;0,1000;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900;1,1000&family=Ruge+Boogie&display=swap');

$max-width: 1000px;
$default-color: #000;
$gray-bg: transparent; //#f7f7fa;
$default-bg-color: rgba(255,255,255, 0.5);

body {
  margin: 0;
  padding: 0;
  font-family: 'Nunito', sans-serif;
  font-size: 1rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #000;
  color: $default-color;

  &::after {
    content: "";
    background: url('../public/bg.jpg') no-repeat transparent;
    background-size: cover;
    opacity: 0.5;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: fixed;
    z-index: -1;   
  }
}

a, label {
  color: $default-color;
}

.App {
  //max-width: $max-width;
  margin: 0 auto;
  padding: 0 0.625rem;
}

.AppHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  //margin: 1rem 0;

  h1 {
    margin: 0;
    padding: 0;
  }

  #logo {
    width: auto;
    height: 7rem;
    margin-right: 0.5rem;
  }

  .header-main {
    display: flex;
    align-items: center;
  }

  .header-main h1 {
    // font-family: 'Ruge Boogie', cursive;
    display: none;
  }
}

.fc-toolbar-title {
  color: #fff;
}

.run-calendar {
  .fc-day {
    background-color: $default-bg-color;
    color: #333;

    &.fc-day-past {
      color: $default-color;
      background: $gray-bg;
      opacity: 0.5;
    }

    .fc-event {
      background: transparent;
      border: none;
      padding: 0.5rem;
      white-space: normal;
      font-weight: bold;
      cursor: pointer;

      .fc-event-main {
        color: $default-color;
      }

      &.fc-event-today {
        .fc-event-main {
          color: #fff;
        }
      }
    }
  }
}

.AppFooter {
  padding: 1rem;
  text-align: center;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;

  .footer-info {
    display: flex;
    justify-content: center;
    gap: 0.5rem;
  }

  * {
    color: rgba(255,255,255, 0.5);
  }
}

.donate {
  display: flex;
  align-items: center;
  justify-content: center;

  .donate-qr-code {
    img {
      max-width: 64px;
      max-height: 64px;
    }
  }
}

.cnd_item_duration {
  font-weight: normal;
}
.cnd_item_content {
  display: none;
}

.MuiPopover-root {
  .cnd_popover_content {
    max-width: 320px;
    padding: 8px;
  }
  .cnd_popover_content_t {
    font-weight: bold;
  }
  .cnd_popover_content_c {
    margin-bottom: 5px;
  }
}